.card-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
    gap: 30px 15px;
    padding-top: 30px;
    margin-block-end: 20px;

}

.filter-description {
    display: flex;
    // align-items: center;
    justify-content: space-between;
}

.sort-form {
    display: flex;
    align-items: center;
    font-size: 1rem;
    
    &>select {
        margin-left: 7px;
        appearance: none;
        border: none;
        outline: none;
        border: none;
        font-family: avenir-black;
        font-size: 1.1rem;
        cursor: pointer;
    }

    // margin-right: 80px;
    // padding-right: 50px;
    // width: 100px;
    // position: relative;
}

.explore-container {
    margin-top: 80px;
}

.explore-search {
    cursor: pointer;
    top: 9px;
    left: 24%;
    align-items: center;
    display: flex;
    padding: 0 0 0 0.6rem;
    color: #1e1e1e;
    font-size: 0.9rem;
    position: fixed;
    z-index: 101;
    border-radius: 200px;
    line-height: 45px;
    border: #dadada 1px solid;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @include for-mobile-layout {
        left: 47%;
    }

    &>.search-btn-explore {
        background-color: #36b99f;
        padding: 4px 3px;
        margin: 1px 7px 1px 19px;
        border-radius: 50%;
        width: 35px;
        height: 35px;

        &>img {
            padding: 3px;
        }
    }
}